import React, { Fragment } from 'react';
import Pulse from 'react-reveal/Pulse';


const About = () => {

    return (
        <Fragment>
            <Pulse duration={3000}>
                <section id="sobre" className="about">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12 text-center">
                                <h1>SHALOM</h1>
                                <h3>Igreja Batista do Avivamento Shalom</h3>
                                <h2>NOSSO TEMA!</h2>
                                <p className="lead">Haver h Shalom, estabilidade nos teus tempos, abundncia de salvao, sabedoria e conhecimento; e o temor do Senhor ser o seu tesouro. Isaas 33:6</p>

                                <h2>CULTOS</h2>
                                <p className="lead">Domingo 18h - Intercesso | 18h30 - Adorao e Palavra</p>
                                <p className="lead">Domingo 19h - Culto Infantil</p>
                                <p className="lead">Segunda-feira 6h s 7h30 - Orao com os Atalaias</p>
                                <p className="lead">Segunda-feira 18h s 19h30 - Orao e Intercesso</p>
                                <p className="lead">Tera-feira 14h30 - Culto de Milagres</p>
                                <p className="lead">Tera-feira 19h30 - Ensaio da Equipe de Louvor</p>
                                <p className="lead">Quarta-feira 19h30 s 21h - Culto da Vitria</p>
                                <p className="lead">Sexta-feira 20h - Orao e Vgilia</p>

                                <h2>ENCONTROS</h2>
                                <p className="lead">Sbado, uma vez por ms, Culto das Mulheres</p>
                                <p className="lead">Sexta-feira - 19h, Clula Teens (Adolescentes</p>
                                <p className="lead">Sbado - 18h, Meetup|JS (Jovens)</p>

                                <h2>CURSOS</h2>
                                <p className="lead">Mulher nica</p>
                                <p className="lead">Homem ao Mximo</p>
                                <p className="lead">Casados para Sempre</p>
                                <p className="lead"><spam className="text-muted">Tem interesse, fale conosco para montarmos uma nova turma</spam></p>

                            </div>
                        </div>
                        {/* <!-- /.row --> */}
                    </div>
                    {/* <!-- /.container --> */}
                </section>
            </Pulse>
        </Fragment>
    )
}

export default About


